/* ==========================================================
! PROFILE
========================================================== */
.section-home {
  z-index: 88;
  opacity: 1;
  overflow-y: auto;
}

.vcard-profile-pic {
  width: 200px;
  height: 200px;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 20px;
  position: relative;
}

.vcard-profile-pic img {
  width: 100%;
  border-radius: 100%;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;
  border: 5px solid #e63900;
  -webkit-transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}

.profileActive {
  opacity: 1 !important;
}

.vcard-profile-description {
  text-align: center;
}

.h2-name {
  padding-bottom: 3px;
}

.h2-name >h2 {
  font-weight: bold;
  color: #021533;
  letter-spacing: -2px;
  font-size: 35px;
  font-family: 'Kaushan Script', cursive;
}

.h2-name >h5 {

  /* font-weight: bold !important;
  color: #021533;
  letter-spacing: -2px; */
  font-size: 17px !important;
  font-family: 'Kaushan Script', cursive;
}

.hr1 {
  border: none;
  height: 1px;
  width: 100%;
  background: url('/assets/img/hr1.png') no-repeat center center;
  margin-top: 2px;
  margin-bottom: -2px;
}

.hr2 {
  border: none;
  height: 1px;
  width: 100%;
  background: url('/assets/img/hr2.png') no-repeat center center;
  margin-top: 10px;
  margin-bottom: 0px;
  padding: 8px;
}

.p-info {
  text-align: center;
  font-size: 15px;
  padding: 15px 0 10px 0;
  letter-spacing: -0.2px;
  font-family: 'Courgette', cursive;
  font-weight: bold;
}

.p-contact {
  font-size: 1.1em;
  background: url('/assets/img/bg-btn.png') no-repeat center center #e63900;
  color: #ffffff;
  margin: 0 0 5px 5px;
  border-radius: 10px;
  padding: 8px;
  text-align: center;
  font-family: 'Kaushan Script', cursive;
  letter-spacing: 0.5px;
}

.p-contact > p {
  margin-bottom: 0px;
}

/*=========================
Icons
 ================= */

/* footer social icons */

.ic-social {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

ul.social-network {
  list-style: none;
  display: inline;
  padding: 0;
}

ul.social-network li {
  display: inline;
  margin: 0 5px;
}

/* footer social icons */
.social-network a.icoFacebook:hover {
  background-color: #3B5998;
}

.social-network a.icoTwitter:hover {
  background-color: #33ccff;
}

.social-network a.icoGoogle:hover {
  background-color: #BD3518;
}

.social-network a.icoVimeo:hover {
  background-color: #0590B8;
}

.social-network a.icoLinkedin:hover {
  background-color: #007bb7;
}

.social-network a.icoRss:hover i,
.social-network a.icoFacebook:hover i,
.social-network a.icoTwitter:hover i,
.social-network a.icoGoogle:hover i,
.social-network a.icoVimeo:hover i,
.social-network a.icoLinkedin:hover i {
  color: #fff;
}

a.socialIcon:hover,
.socialHoverClass {
  color: #44BCDD;
}

.social-circle li a {
  display: inline-block;
  position: relative;
  margin: 0 auto 0 auto;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  text-align: center;
  width: 50px;
  height: 50px;
  font-size: 20px;
  border: 2px solid #e63900;
}

.social-circle li i {
  margin: 0;
  line-height: 50px;
  text-align: center;
}

.social-circle li a:hover i,
.triggeredHover {
  -moz-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
  -ms--transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  -o-transition: all 0.2s;
  -ms-transition: all 0.2s;
  transition: all 0.2s;
}

.social-circle i {
  color: #fff;
  -webkit-transition: all 0.8s;
  -moz-transition: all 0.8s;
  -o-transition: all 0.8s;
  -ms-transition: all 0.8s;
  transition: all 0.8s;
}

a {
  background-color: #D3D3D3;
}

.social-icons {
  font-size: 28px;
  text-align: right;
  display: none;
}

@media (min-width:768px) {
  .social-icons {
    display: block;
  }

}

.social-icons a {
  color: #ffffff;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  opacity: 1;
}

.social-icons a:hover {
  opacity: 0.7;
}

.footer-social-icons {
  text-align: center;
}

.footer-social-icons a {
  color: #ffffff;
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  background: #333333;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  font-size: 14px;
  line-height: 30px;
  border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-border-radius: 0px;
}

.footer-social-icons a:hover {
  color: #ffffff;
  background: #08aeac;
}