  padding-bottom: 0px;
  margin-bottom: 0px;
  font-family: 'Acme', cursive;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.section-title2 {
  font-weight: bold;
  font-family: 'Acme', cursive;
  color: #021533;
  font-size: 22px;
  line-height: 24px;
  letter-spacing: 1px;
  padding-bottom: 10px;
  padding-left: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  border-left: 4px solid #e63900;
}

.resume-item {
  padding: 15px;
  border: 4px double #ccc;
  margin-bottom: 15px;
}

.section-item-title-1 {
  font-weight: bold;
  color: #021533;
  color: #333;
  padding-left: 0;
  margin-left: 0;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  margin-bottom: 8px;
  letter-spacing: -1px;
  font-family: 'Acme', cursive;
}

.section-item-title-2 {
  font-weight: bold;
  color: #021533;
  color: #333;
  padding-left: 0;
  margin-left: 0;
  font-size: 18px;
  line-height: 22px;
  margin: 0;
  margin-bottom: 8px;
  letter-spacing: -1px;
  border-bottom: 1px dotted #021533;
  padding-bottom: 5px;
  font-size: 15px;
}

.graduation-time,
.job {
  line-height: 15px;
  font-style: italic;
  color: #555;
  font-size: 10px;
  margin-top: 5px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 5px;
}

.graduation-description ul {
  font-size: 12px;
  font-family: 'Acme', cursive;
  padding-left: 20px;
  text-align: left;
}

.graduation-description span {
  font-size: 13px;
  font-family: 'Acme', cursive;
  text-align: left !important;
  padding-bottom: 5px;
  font-weight: bold;
}

.graduation-description {
  font-size: 13px;
  font-family: 'Acme', cursive;
  padding-left: 20px;
  text-align: left;
}

/*============================================================================
>>  SECTION: Resume
============================================================================
*/
.section-education {
  margin-bottom: 40px;
  margin-top: -25px;
}

.resume-buttons {
  margin-bottom: 30px;
}

.header-page-buttons {
  margin-top: -20px;
}

.page-footer {
  border-top: 1px solid #ccc;
  padding-top: 20px;
}

.footer-quote {
  color: #444;
  font-size: 11px;
  border-top: 1px solid #ccc;
  padding-top: 10px;
}

.testimonials {
  text-align: center;
  font-size: 18px;
}

.testimonial-item {
  width: 100%;
  margin: 0 auto;
}

@media (min-width:768px) {
  .testimonial-item {
    padding: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

}

.quote-text {
  font-style: italic;
}

.testimonial-credits {
  margin-top: 30px;
}

.testimonial-author {
  font-size: 15px;
  margin-bottom: 0;
  margin-top: 15px;
  font-weight: bold;
}

.testimonial-content {
  background: url('../img/bgmask2.html') no-repeat center center #08aeac;
  color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  -moz-border-radius: 10px;
  -webkit-border-radius: 10px;
  width: 100%;
  position: relative;
}

.testimonial-content p {
  font-size: 13px;
  line-height: 16px;
}

.testimonial-content:after {
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  position: absolute;
  left: 50%;
  bottom: -5px;
  margin-left: -5px;
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  z-index: 888;
  background: #08aeac;
}

.testimonial-picture {
  width: 110px;
  height: 110px;
  overflow: hidden;
  margin: 0 auto;
}

.testimonial-picture img {
  width: 100%;
  border-radius: 100%;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;
  border: 6px solid #ddd;
}

.testimonial-firm {
  font-size: 10px;
  margin-bottom: 0;
  text-transform: uppercase;
  font-style: italic;
  font-weight: lighter;
}

.owl-carousel {
  cursor: -webkit-grab;
  cursor: -moz-grab;
}

.owl-carousel:focus {
  cursor: -webkit-grab;
  cursor: -moz-grab;
}

.owl-carousel:active {
  cursor: -webkit-grab;
  cursor: -moz-grab;
}

.owl-carousel:hover {
  cursor: -webkit-grab;
  cursor: -moz-grab;
}