/* ==========================================================
! 12. SECTION: SKILLS
========================================================== */
.bx-pro {
  margin: 10px 0 -15px 35px;
  display: flex;
  justify-content: flex-start;
  font-family: 'Kaushan Script', cursive;
  font-weight: 800 !important;
  letter-spacing: 1px;
  color: #021533;
}

.percent {
  padding-top: 3px;
  position: absolute;
  right: 48px;
  font-size: 17px;
}

.progress-bar {
  background: url('/assets/img/bg-card2.png') repeat-x right top #f4f4f4;
  height: 30px;
  width: 360px;
  margin: 0 0 20px -35px;
  border-radius: 2px;
  text-align: left;
}

.progress-bar span {
  display: inline-block;
  height: 100%;
  background-color: #777;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -moz-box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
  -webkit-box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
  box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
  -webkit-transition: width .4s ease-in-out;
  -moz-transition: width .4s ease-in-out;
  -ms-transition: width .4s ease-in-out;
  -o-transition: width .4s ease-in-out;
  transition: width .4s ease-in-out;
  padding: 10px;
}

/*---------------------------*/
.orange span {
  background-color: #fecf23;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fecf23), to(#e63900));
  background-image: -webkit-linear-gradient(top, rgb(191, 158, 35), #e63900);
  background-image: -moz-linear-gradient(top, #fecf23, #fd9215);
  background-image: -ms-linear-gradient(top, #fecf23, #fd9215);
  background-image: -o-linear-gradient(top, #fecf23, #fd9215);
  background-image: linear-gradient(top, #fecf23, #fd9215);
}

.red span {
  background-color: #fecf23;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#fecf23), to(#e63900));
  background-image: -webkit-linear-gradient(top, rgb(144, 31, 31), #fe0200);
  background-image: -moz-linear-gradient(top, rgb(144, 31, 31), #fe0200);
  background-image: -ms-linear-gradient(top, rgb(144, 31, 31), #fe0200);
  background-image: -o-linear-gradient(top, rgb(144, 31, 31), #fe0200);
  background-image: linear-gradient(top, rgb(144, 31, 31), #fe0200);
}


.blue span {
  background-image: -webkit-linear-gradient(top,#3f5775,#17202d);
}

.green span {

 background-image: -webkit-gradient(linear,left top,left bottom,from(rgba(33, 142, 67, 0.61)),to(#013315));
  
}

.shine span {
  position: relative;
}

.shine span::after {
  content: '';
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  border-radius: 3px;

}


.grow { transition: all .3s ease-in-out; }
.grow:hover { 
    transform: scale(1.1);
    cursor: pointer; cursor: hand;
}

@-webkit-keyframes animate-shine {
  0% {
    opacity: 0;
    width: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 0;
    width: 95%;
  }

}

@-moz-keyframes animate-shine {
  0% {
    opacity: 0;
    width: 0;
  }

  50% {
    opacity: .5;
  }

  100% {
    opacity: 0;
    width: 95%;
  }

}


.title-pro {
  color: #fff;
  position: relative;
  bottom: 6px;
  left: 5px;
  font-size: 13px;
   font-family: 'Acme', cursive;
}


.section-item-title-2 {
  color: #021533;
  padding-left: 0;
  margin-left: 0;
  font-size: 16px;
  line-height: 22px;
  margin-bottom: 8px;
  letter-spacing: 0.7px;
  font-family: 'Acme', cursive;
}

/* END Keyframes for the initial animation */