/* ==========================================================
! 01. MIXINS / VARIABLES
==============  =========================================== */
@import url('https://fonts.googleapis.com/css?family=Courgette');
@import url('https://fonts.googleapis.com/css?family=Luckiest+Guy');
@import url('https://fonts.googleapis.com/css?family=Kaushan+Script');
@import url('https://fonts.googleapis.com/css?family=Acme');
@import url('https://fonts.googleapis.com/css?family=Dancing+Script');

.color1 {
  color: #e63900;
}

.color2 {
  color: #021533;
}

.color3 {
  color: #d9212a;
}

.bold {
  font-weight: bold;
}

.bg-cover {
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}

.box-shadow {
  -webkit-box-shadow: 0px 2px 6px 0px rgba(50, 50, 50, 0.3);
  -moz-box-shadow: 0px 2px 6px 0px rgba(50, 50, 50, 0.3);
  box-shadow: 0px 2px 6px 0px rgba(50, 50, 50, 0.3);
}

.responsive-image {
  width: 100%;
}

.section {
  padding-top: 20px;
  padding-bottom: 20px;
}

/* ==========================================================
! 02. GENERAL 
========================================================== */
body {
  -webkit-font-smoothing: subpixel-antialiased;
  background: url('/assets/img/asd2.jpg') no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;  /* overflow: hidden; */
}

html, body{
  // overflow: hidden;
}


/* ==========================================================
! 06. SECTION: vCard Body
========================================================== */
.container {
  position: relative;
  padding-bottom: 80px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@media (min-width:992px) {
  .container {
    min-height: 680px;
  }

}

.section-vcardbody {
  width: 99%;
  margin: 0 auto;
  overflow-y: scroll;
  overflow-x: hidden;
  background: url('/assets/img/bg-card2.png') repeat-x right top #f4f4f4;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px;
  -webkit-transition: all 0.6s ease-in-out;
  -moz-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
  border-radius: 0px;
  -moz-border-radius: 0px;
  -webkit-border-radius: 0px;
  -webkit-box-shadow: 0px 0px 17px 5px rgba(0, 0, 0, 0.78);
  -moz-box-shadow: 0px 0px 17px 5px rgba(0, 0, 0, 0.78);
  box-shadow: 0px 0px 17px 5px rgba(0, 0, 0, 0.78);
}

@media (min-width:992px) {
  .section-vcardbody {
    position: absolute;
    margin-top: 30px;
    left: 30%;
    height: 570px;
    width: 40%;
    padding: 45px;
    padding-bottom: 20px;
  }

}

@media (min-width:1400px) {
  .section-vcardbody {
    margin-top: 180px;
  }

}

.section-vcardbody-pgactive {
  left: 15%;
}

@media (min-width:992px) {
  .section-page-single {
    left: 55%;
    transform: translateY(-200px);
    -moz-transform: translateY(-200px);
    -webkit-transform: translateY(-200px);
    visibility: hidden;
    padding: 20px;
    padding-top: 0px;
  }

}

@media (min-width:992px) {
  .section-page {
    z-index: 11;
    opacity: 0;
  }

}

@media (min-width:992px) {
  .page-mask {
    position: absolute;
    top: 605px;
    left: 30%;
    display: block;
    width: 40%;
    height: 20px;
    background: #333;
    z-index: 77;
    opacity: 0;
    -webkit-transition: all 0.6s ease-in-out;
    -moz-transition: all 0.6s ease-in-out;
    transition: all 0.6s ease-in-out;
  }

}

.page-mask-active {
  left: 55%;
  opacity: 1;
  margin-left: 10px;
  border: 3px red solid;
}

.section-page-active {
  opacity: 1;
  z-index: 22;
  left: 55%;
  visibility: visible;
  transform: translateY(0px);
  -moz-transform: translateY(0px);
  -webkit-transform: translateY(0px);
}

@media (min-width:992px) {
  .section-page-active {
    margin-top: 30px;
    margin-left: 10px;
  }

  .h2-name >h2 {
    font-size: 30px;
  }

}

@media (min-width:1400px) {
  .section-page-active {
    margin-top: 180px;
  }

}

