/* ==========================================================
! 0. Hobbie and interests
========================================================== */
.cont {
  display: flex;
  justify-content: center;
  margin-top: 100px;
}

/*Portfolio*/
.portfolio-item a {
  background-color: #f2f2f3;
  display: inline-block;
  padding: 20px;
  border: 5px solid #272425;
}

.portfolio-holder .portfolio-item a,
.portfolio-holder .portfolio-item a img {
  border-radius: 600px;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.portfolio-holder .portfolio-item a img {
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  border: 2px solid #272425;
}

.portfolio-holder .portfolio-item a:hover {
  background-color: #e63900;
}

.portfolio-holder .portfolio-item a:hover img {
  filter: grayscale(0%);
  -webkit-filter: grayscale(0%);
  -moz-filter: grayscale(0%);
  -o-filter: grayscale(0%);
  -ms-filter: grayscale(0%);
  transform: scale(1.1);
}